.sidebar{
    background-color: #111;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    padding: 16px 32px;
    position: relative;
}


.sidebar button.upload{
    margin-top: auto;
}

.sidebar .center{
    background-color: #222;
}

input{
    width: 70px;
    padding: 8px;
    border-radius: 6px;
}

input[type="color"]{
    padding: 4px;
    height: 40px;
    border: none;
    background-color: transparent;
    box-shadow: none;
    border-radius: 8px;
    overflow: hidden;
}
.prop{
    display: grid;
    grid-template-columns: 65px 1fr;
    align-items: center;
    gap: 16px;
    width: 100%;
}

.prop p{
    font-size: 14px;
}

.prop input{
    width: 100%;
}

