@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    line-height: 1.6;
    background-color:#222;
    color: #fff;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
        "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

input {
    line-height: 1.2;
    background-color:#222;
    height: 40px;
    flex-shrink: 0;
    color: #fff;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
        "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    outline: none;
    border:1px solid #222;
    box-shadow: 0 2px 8px rgba(0,0,0,0.015);
}

input:focus{
    border-color: #444;
}

button{
    width: 100%;
    border: none;
    background-color: #05f;
    color: #fff;
    border-radius: 8px;
    height: 40px;
    flex-shrink: 0;
    font-size: 16px;
    text-transform: uppercase;
    cursor: pointer;
    font-family: "Poppins";
}

button:active{
    transform: scale(0.98);
}

button:disabled{
    opacity: 0.5;
    cursor: not-allowed;
}
.App{
    display: grid;
    grid-template-columns: auto 300px;
    align-items: flex-start;
    height: 100vh;
}
@media (min-width:0)and (max-width:768px){
    .App{

        grid-template-columns: 1fr;
        grid-template-rows: 2fr auto;
    }
    .sidebar{
        overflow-y: scroll;
        padding: 32px;
        border-radius: 16px 16px 0 0 ;
    }
}
.editor{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.login{
    background-color: #111;
    max-width: 400px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 16px;
    border-radius: 8px;
    top: 50%;
    left:50%;
    width: 100%;
    position: absolute;
    transform: translate(-50%,-50%);
    grid-gap: 16px;
    gap: 16px;
}

.login input{
    width: 100%;
}
.editor{
    position: relative;
}
.editor .text {
    position: absolute;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    touch-action: none;
    transform: translate(-50%);
}


.container{
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}


.sidebar{
    background-color: #111;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
    padding: 16px 32px;
    position: relative;
}


.sidebar button.upload{
    margin-top: auto;
}

.sidebar .center{
    background-color: #222;
}

input{
    width: 70px;
    padding: 8px;
    border-radius: 6px;
}

input[type="color"]{
    padding: 4px;
    height: 40px;
    border: none;
    background-color: transparent;
    box-shadow: none;
    border-radius: 8px;
    overflow: hidden;
}
.prop{
    display: grid;
    grid-template-columns: 65px 1fr;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
    width: 100%;
}

.prop p{
    font-size: 14px;
}

.prop input{
    width: 100%;
}


